$primaryColor: #4527a4;

// Common CSS
html {
	scroll-behavior: smooth;
}

// Dashicon
.dashicons {
	font-family: dashicons !important;
}

//Directory
.wp-block-ttp-tiktok-player {

	.ttptiktok {
		* {
			box-sizing: border-box;
		}

		a,
		button {
			text-decoration: none;
		}

		.ttpEmptyVideo {
			h3 {
				margin: 0px;
				font-size: 29px;
				text-align: center;
				font-weight: 700;
				padding: 29px;
				color: #000;
				background: rgba(238, 240, 241, 0.16);
				backdrop-filter: blur(15.8px);
				-webkit-backdrop-filter: blur(15.8px);
				border: 1px solid rgba(238, 240, 241, 0.3);
			}
		}

		.ttpfeedItemArea {
			position: relative;

			.feedMainArea {

				.feedInfoArea {
					width: 100%;
					height: 100%;
					--webkit-backdrop-filter: blur(7.5px);
					backdrop-filter: blur(5.5px);
					position: absolute;
					top: 0;
					left: 0;
					opacity: 0;
					transition: 0.3s;
					border-radius: 3px;

					.feedItemInfo {
						display: flex;
						align-items: center;
						justify-content: center;
						column-gap: 5px;
						position: absolute;
						top: 0;
						left: 0;
						width: 100%;
						height: 100%;

						p {
							display: flex;
							align-items: center;
							justify-content: center;
							column-gap: 1px;
							margin: 0px;

							span {
								color: #fff;
							}
						}
					}

					.feedPlayCount {
						position: absolute;
						width: 100%;
						height: 100%;
						left: 0;
						top: 0;
						padding: 10px 0px 0px 10px;

						p {
							margin: 0px;
							display: flex;
							align-items: center;
							column-gap: 5px;

							span {
								color: #fff;
							}
						}
					}
				}

				.feedItem {
					height: 100%;
					position: relative;
					cursor: pointer;

					.feedItemImg {
						width: 100%;

						img {
							width: 100%;
							height: 100%;
							object-fit: cover;
							border-radius: 3px;
						}
					}

					.square {
						position: relative;
						width: 100%;

						img {
							position: absolute;
							top: 0;
							left: 0;
							bottom: 0;
							right: 0;
							text-align: center;
						}
					}
				}
			}

			&.default {
				display: grid;

				.feedMainArea {
					.feedItem {
						.feedItemImg {
							height: 100%;
						}

						.feedInfoArea {

							&:hover {
								opacity: 1;
								transition: 0.3s;
							}
						}
					}
				}
			}

			&.masonry {

				.feedMainArea {

					&:hover .feedItem .feedInfoArea {
						opacity: 1;
					}

					.feedItem {
						break-inside: avoid;

						.feedItemImg {
							position: relative;
							line-height: initial;
						}

						.feedInfoArea {}
					}

					.footerInfo {
						.top {
							display: flex;
							justify-content: space-between;
							padding-bottom: 10px;
							color: inherit;

							.time {
								font-size: 13px;
								font-weight: 400;
								color: inherit;
							}

							.info {
								display: flex;
								column-gap: 3px;
								align-items: center;
								color: inherit;

								p {
									margin: 0;
									display: flex;
									align-items: center;
									column-gap: 3px;
									color: inherit;
								}

								span {}
							}
						}
					}

					.title {
						padding-top: 15px;

						p {
							font-size: 14px;
							font-weight: 400;
							margin: 0;
						}
					}
				}
			}

			&.slider {
				display: unset;

				.swiper {

					.swiper-button-prev,
					.swiper-button-next {
						color: #fff;

					}
				}

				.feedItemImg {
					height: 100%;
					line-height: initial;
				}

				.feedInfoArea {

					&:hover {
						opacity: 1;
						transition: 0.3s;
					}
				}
			}
		}

		.ttpHasMore {
			display: flex;
			align-items: center;
			justify-content: center;
			column-gap: 15px;

			a {
				display: inline-block;
				border: 1px solid transparent;
				padding: 9px 16px;
				border-radius: 3px;
				cursor: pointer;
				line-height: 1;
				transition: 0.3s;
				text-transform: capitalize;
				margin-top: 20px;
				text-decoration: none;

				display: flex;
				align-items: center;
				column-gap: 10px;

				&:hover {
					border-color: #000;
					transition: 0.3s;
				}
			}

			button {
				display: inline-block;
				border: 1px solid transparent;
				padding: 9px 16px;
				border-radius: 3px;
				cursor: pointer;
				line-height: 1;
				transition: 0.3s;
				text-transform: capitalize;
				margin-top: 20px;

				&:hover {
					border-color: #000;
					transition: 0.3s;
				}
			}
		}

		.contentPreloader {
			text-align: center;

			svg {
				width: 70px;
				height: 70px;
			}
		}

		.ttpProfile {
			background-color: #000;
			padding: 10px 20px;
			margin-bottom: 15px;

			// Default Style 
			.userProfileInfo {
				display: flex;
				flex-direction: column;

				.profileInfo {
					display: flex;
					align-items: center;
					column-gap: 20px;

					.profileImg {
						img {
							width: 110px;
							height: 110px;
							border-radius: 50%;
						}
					}

					.displayNameShare {
						a {
							text-transform: capitalize;
							text-decoration: none;

							&:nth-child(1) {
								display: block;
							}

						}

						.ttpShareBtn {
							text-decoration: none;
							border-radius: 3px;
							text-align: center;
							transition: 0.3s;
							display: inline-block;
							min-width: 208px;
							text-transform: uppercase;
							margin-top: 10px;
						}
					}
				}

				.social_count {
					margin-top: 20px;
					margin-bottom: 15px;

					ul {
						margin: 0px;
						padding: 0;
						display: flex;
						align-items: center;

						li {
							margin: 0;
							padding: 0;
							list-style: none;
							font-size: 16px;
							font-weight: 400;
							text-transform: capitalize;
							margin-right: 8px;

							span {
								padding-right: 4px;
								font-weight: bold;
								font-size: 18px;
								text-transform: uppercase;
							}
						}
					}
				}

				.user_bio {
					p {
						text-align: left;
						font-weight: 400;
						font-size: 16px;
						line-height: 22px;
						white-space: pre-line;
						width: 300px;
						margin: 0px;
						text-align: center;
					}
				}
			}

			// Card Style 
			.CarduserProfileInfo {
				text-align: center;

				.profileImg {
					img {
						width: 100px;
						height: 100px;
						border-radius: 50%;
					}
				}

				.profileInfo {
					.displayNameShare {
						display: flex;
						align-items: center;
						justify-content: center;
						flex-direction: column;

						a {
							text-transform: capitalize;
							text-decoration: none;
						}

						.ttpShareBtn {
							text-decoration: none;
							border-radius: 3px;
							min-width: 208px;
							margin: 5px 0px;
						}
					}

					.social_share {
						ul {
							margin: 0px;
							padding: 0;
							display: flex;
							align-items: center;
							justify-content: center;
							margin-top: 10px;

							li {
								margin: 0;
								padding: 0;
								list-style: none;
								font-size: 14px;
								font-weight: 400;
								text-transform: capitalize;
								border-right: 2px solid #000;
								padding-right: 5px;
								margin-right: 5px;
								line-height: 12px;
								display: flex;
								flex-direction: column;
								align-items: center;
								row-gap: 4px;

								span {
									font-weight: 500;
									padding-right: 4px;
								}
							}

							:last-child {
								border-right: none;
							}
						}
					}
				}
			}

			// Compact Style 
			.CompactuserProfileInfo {
				display: inline-block;

				.CompactuserProfileimgbtn {
					display: flex;
					align-items: center;
					padding: 6px 10px;
					column-gap: 10px;
					border-radius: 3px 3px 0px 0px;
					box-sizing: border-box;

					.profileImg {
						max-width: 30px;
						max-height: 30px;

						img {
							width: 100%;
							height: 100%;
							border-radius: 50%;
						}
					}

					.profileInfo {
						width: 100%;

						.displayNameShare {
							display: flex;
							align-items: center;
							justify-content: space-between;
							column-gap: 5px;

							a {
								text-transform: capitalize;
								text-decoration: none;
								line-height: 1.35em;
							}

							.ttpShareBtn {
								text-decoration: none;
								border-radius: 3px;
								display: block;
							}
						}
					}
				}

				.social_share {
					display: inline-block;
					padding: 8px 10px;
					border-radius: 0px 0px 3px 3px;
					width: 100%;
					display: block;
					box-sizing: border-box;

					ul {
						margin: 0px;
						padding: 0;
						display: flex;
						align-items: center;
						justify-content: center;

						li {
							margin: 0;
							padding: 0;
							list-style: none;
							font-size: 14px;
							font-weight: 400;
							text-transform: capitalize;
							border-right: 2px solid #fff;
							padding-right: 5px;
							margin-right: 5px;
							line-height: 12px;
							display: flex;
							flex-direction: column;
							align-items: center;
							row-gap: 4px;

							span {
								font-weight: 500;
								padding-right: 4px;
							}
						}

						:last-child {
							border-right: none;
						}
					}
				}

			}
		}
	}

	.icon {
		font-style: inherit;
	}

	img {
		width: 100%;
		max-width: 100%;
	}
}

// Modal css 
.modal_content {
	display: none;
	border-radius: 3px;
}

.modal_content_masonry {
	display: none;
	border-radius: 3px;
	padding: 10px 20px;

	.feedPopUpArea {
		display: flex;
		column-gap: 20px;

		.feedIframe {
			width: 500px;

			iframe {
				width: 100%;
				height: 570px;
				display: block;
				visibility: unset;
			}
		}

		.feedInfo {
			.profile {
				display: flex;
				align-items: center;
				column-gap: 20px;

				.img {
					width: 60px;
					height: 60px;

					img {
						width: 100%;
						height: 100%;
						object-fit: cover;
						border-radius: 50%;
					}
				}

				.name {
					color: inherit;

					h3 {
						margin: 0;
						font-size: 18px;
						font-weight: 500;
						color: inherit;
					}

					a {
						color: inherit;
						text-decoration: none;
						display: block;
						font-size: 14px;
						font-weight: 400;
						text-transform: lowercase;
						;
					}
				}

				padding-bottom: 20px;
				border-bottom: 1px solid #0000003d;
			}

			.body {
				.body_icons_title {
					color: inherit;

					ul {
						list-style: none;
						display: flex;
						margin: 0;
						padding: 0;
						column-gap: 30px;

						li {
							color: inherit;
							font-size: 13px;
							font-weight: 400;

							a {
								color: inherit;
								text-decoration: none;
								display: flex;
								align-items: center;
								column-gap: 1px;
							}
						}
					}

					.title {
						font-size: 16px;
						font-weight: 400;
						text-transform: capitalize;
						margin-top: 30px;
						text-align: right;
					}

					padding: 20px 0px;
					border-bottom: 1px solid #0000003d;
				}
			}

			.footer {
				margin-top: 15px;
				display: flex;
				align-items: center;
				justify-content: space-between;
				column-gap: 10px;

				.icons {
					display: flex;
					align-items: center;
					column-gap: 3px;

					p {
						margin: 0;
						font-size: 13px;
						font-weight: 400;
						display: flex;
						align-items: center;
						column-gap: 1px;
					}
				}

				.time {
					font-size: 13px;
					font-weight: 500;
				}
			}
		}
	}
}

/*
*
*column set
*/
@for $i from 1 through 12 {
	.columns-#{$i} {
		grid-template-columns: repeat(#{$i}, 1fr);
		columns: $i;
	}
}

@media (max-width: 768px) {
	@for $i from 1 through 12 {
		.columns-tablet-#{$i} {
			grid-template-columns: repeat(#{$i}, 1fr);
			columns: $i;
		}
	}
}

@media (max-width: 576px) {
	@for $i from 1 through 12 {
		.columns-mobile-#{$i} {
			grid-template-columns: repeat(#{$i}, 1fr);
			columns: $i;
		}
	}
}